
import { TabContainer, TabContainerItem } from "mint-ui"
import XHeader from 'vux/src/components/x-header/index.vue'

import "@/assets/css/main.css"

let count=1;
export default {
name: "mystar",
components: {
XHeader,
TabContainer,
TabContainerItem,
},
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
msg: "这个是信用贷页面",

gr:[],
arrow:true,
alert:false,
active: "sq_content",
alertimg:[{src:require("@/assets/images/my/alert-icon3.png"),icon:"态度热情，专业能力很强，细致耐心。"},{src:require("@/assets/images/my/alert-icon2.png"),icon:"专业能力极强，客户一致好评，值得信赖。"},{src:require("@/assets/images/my/alert-icon1.png"),icon:"最靠谱最贴心的伙伴，好评如潮，值得亲睐。"}],
cityselected:0,
cities: [ ],
timeselected:0,
times:[]  ,
moneyselected:0,
moneys:[],
scrollbarObj: {
fade: true
},

pullDownRefreshObj: {
threshold: 90,
stop: 40
},

pullUpLoadObj: {
threshold: 0,
txt: {
more: '加载更多',
noMore: '没有更多数据了'
}
},
startY: 0,  // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
};
},
methods: {
tabBtnClick(type) {
this.active = type;
},
hidemsg:function(){
this.alert=false;
},
alertmsg(ev){
ev=ev||window.event;
ev.preventDefault();
this.alert=true;
}, // 滚动到页面顶部
scrollTo () {
this.$refs.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
},
getData () {// 模拟数据请求
return new Promise(resolve => {
var _this=this;
setTimeout(() => {
var getarr = [];
_this.$axios.post('/api/Usercenter/my_collect',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
type:'product'
})}`).then((res)=>{

if(res.code==200){
getarr=res.data.list;
resolve(getarr)
}
}).catch((err)=>{throw err});

}, 1000)
})
},
onPullingDown () {

var _this=this;

count = 0;
this.getData().then(res => {

_this.items = res;

})
},
onPullingUp () {


this.getData().then(res => {
this.items = this.items.concat(res)
if(count<50){
}else{
}
})
},
},

created(){












},mounted(){


this.onPullingDown();
},beforeCreate(){

}
};



